import * as React from 'react';
import { EnhancedMultipleSelect } from 'oned-react';

function wait(timeout = 1000) {
  return new Promise(resolve => {
    setTimeout(resolve, 1000);
  });
}

class MultipleDemo extends React.Component<{}, {}> {
  public render() {
    return (
      <EnhancedMultipleSelect
        placeholder="请搜索人名"
        fetchData={(keyword: string) => {
          return fetch('https://randomuser.me/api/?results=5')
            .then(res => res.json())
            .then(body => {
              return body.results.map(user => ({
                text: `${user.name.first} ${user.name.last}`,
                value: user.login.username,
              }));
            });
        }}
        onChange={(...args) => {
          console.log(...args);
        }}
        style={{ width: 500 }}
      />
    );
  }
}

export default <MultipleDemo />;

