import * as React from 'react';
import { EnhancedSelect } from 'oned-react';

function wait(timeout = 1000) {
  return new Promise(resolve => {
    setTimeout(resolve, 1000);
  });
}

class Demo extends React.Component<{}, {}> {
  render() {
    return (
      <EnhancedSelect
        value={{ text: 'label1', value: 1 }}
        fetchData={async (keyword: string) => {
          await wait();
          return [2, 3, 4, 5].map(num => ({ text: 'label' + num, value: num }));
        }}
        style={{ width: 500 }}
      />
    );
  }
}

export default <Demo />;

