import * as React from 'react';
import { EnhancedSelect } from 'oned-react';

class Demo extends React.Component<{}, {}> {
  render() {
    return (
      <EnhancedSelect
        defaultValue="apple"
        data={['apple', 'banana', 'orange', 'pear'].map(name => ({ text: 'fruit_' + name, value: name }))}
        style={{ width: 500 }}
      />
    );
  }
}

export default <Demo />;

